import * as React from 'react'

import { useMutation } from '@apollo/react-hooks'
import { DialogSelect } from '@src/components/DialogSelect'

import {
  GetTransactions_categories as ICategory,
  GetTransactions_transactions as IUserTransaction,
} from '@src/queries/__generated__/GetTransactions.hooks'

import {
  QUERY as MUTATION,
  UpdateTransaction,
  UpdateTransactionVariables,
} from '@src/queries/__generated__/UpdateTransaction.hooks'

interface CategoryFieldProp {
  transaction: IUserTransaction
  selectableCategories: Array<ICategory>
}

export const CategoryField: React.FC<CategoryFieldProp> = (props) => {
  const { selectableCategories, transaction } = props
  const [updateTransaction, { loading }] = useMutation<
    UpdateTransaction,
    UpdateTransactionVariables
  >(MUTATION)
  if (!selectableCategories.length) return <div></div>
  if (loading) return <div>updating...</div>

  return (
    <DialogSelect
      value={transaction.categoryId || null}
      onSelectOption={(option) => {
        updateTransaction({
          variables: { id: transaction.id, categoryId: option.value },
        })
      }}
      choices={selectableCategories.map((cat) => {
        return {
          value: cat.id,
          label: cat.description,
          mui_icon_id: cat.mui_icon_id || '',
        }
      })}
    />
  )
}
